.button-socials {
  // position: absolute;
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%, -50%);
  margin: 0 auto;
  padding: 0;
  display: flex;
  margin-bottom: 20px;

  li {
    list-style: none;

    span {
      position: relative;
      width: 60px;
      height: 60px;
      display: block;
      text-align: center;
      margin: 0 10px;
      border-radius: 50%;
      padding: 6px;
      box-sizing: border-box;
      text-decoration: none;
      box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
      background: linear-gradient(0deg, #ddd, #fff);
      transition: 0.5s;

      &:hover {
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
        text-decoration: none;
      }

      .fab {
        width: 100%;
        height: 100%;
        display: block;
        background: linear-gradient(0deg, #fff, #ddd);
        border-radius: 50%;
        line-height: calc(60px - 12px);
        font-size: 24px;
        color: #262626;
        transition: 0.5s;
      }
    }

    .facebook {
      &:hover {
        .fab {
          color: #3b5998;
        }
      }
    }

    .twitter {
      &:hover {
        .fab {
          color: #00aced;
        }
      }
    }

    .google {
      &:hover {
        .fab {
          color: #dd4b39;
        }
      }
    }

    .linkedin {
      &:hover {
        .fab {
          color: #007bb6;
        }
      }
    }

    .instagram {
      &:hover {
        .fab {
          color: #e4405f;
        }
      }
    }
  }
}
